//---------------------------------------------
// 2. Page Layout
//---------------------------------------------

html {
    scroll-padding-top: 6rem;
}

$hero-background-image: url(../images/background/background.avif);

// hero CSS
#hero {
    width: 100%;
    height: 100vh;
    position: relative;
    background-image: $hero-background-image;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-color: var(--bs-dark);
}

#hero .hero-container {
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 15px;
}

// header CSS
header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1000;
}

.navbar-toggler {
    border: 0;
}

.navbar {
    transition: all 0.5s;
    height: 80px;
}

.navbar-scrolled {
    background-image: $hero-background-image;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0 3px 10px rgba(0,0,0,0.15);
}

// for background
.bg-gray {
    background-color: #f8f8f8;
}

// for SNS icon
.sns-icon {
    height: 2rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
}

// button_line004
.button_line004 {
    a {
        position: relative;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 0 auto;
        max-width: 200px;
        padding: 10px 25px;
        color: #fff;
        transition: 0.3s ease-in-out;
        font-weight: 600;
        background-color: $zuiko-red;
    }
    a:before,
    a:after {
        content: "";
        width: 18px;
        height: 18px;
        border-color: $zuiko-red;
        box-sizing: border-box;
        border-style: solid;
        display: block;
        position: absolute;
        transition: all 0.3s ease-in-out;
    }
    a:before {
        top: -6px;
        left: -6px;
        border-width: 2px 0 0 2px;
        z-index: 5;
    }
    a:after {
        bottom: -6px;
        right: -6px;
        border-width: 0 2px 2px 0;
    }
    a:hover:before,
    a:hover:after {
        width: calc(100% + 12px);
        height: calc(100% + 12px);
        border-color: $zuiko-red;
    }
    a:hover {
        color: #fff;
        background-color: $zuiko-red;
        border-color: $zuiko-red;
    }
}

// a CSS
a {
    text-decoration: none;
}

a:hover {
    color: $zuiko-red;
}

.decoration {
    text-decoration: underline;
}

// font settings
.wf-notosansjapanese {
    font-family: 'Noto Sans JP', sans-serif;
}

.wf-montserrat {
    font-family: "Montserrat", sans-serif;
}

.text-zuiko-red {
    color: $zuiko-red;
}

.info-arrow {
    font-size: $h3-font-size;
}

.info-arrow:hover {
    color: $zuiko-red;
}

